nav ul {
  list-style-type: none;
  overflow: hidden;
  background-color: #333;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline;
  float: left;
  border-right: 1px solid #bbb;
}

nav a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

nav li a:hover {
  background-color: #111;
}

li:last-child {
  border-right: none;
}